import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import CreditsCheckout from 'Employer/components/EmployerBuyCredits/CreditsCheckout'
import { getCVSearchBasePrice, getPaymentCurrency } from 'Shared/selectors/geolocation'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'
import { saveOrderRefIfNeeded } from 'Shared/ducks/payment'

const mapStateToProps = (state, ownProps) => ({
  price: getCVSearchBasePrice(state),
  currency: getPaymentCurrency(state),
  isAuthenticated: state.user.isAuthenticated,
  orderRef: ownProps.match.params.orderRef,
  employerId: state.user.details.id
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded()),
  saveOrderRefIfNeeded: () => dispatch(saveOrderRefIfNeeded(ownProps.match.params.orderRef))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CreditsCheckout))



