import React, { Component, Fragment } from 'react'
import { Helmet } from 'react-helmet'
import Order from 'Shared/components/Order'
import withTranslations from 'Shared/hoc/withTranslations'
import PaymentService from 'Shared/services/PaymentService'
import { PaymentStatuses, TelrOrderStatusCodes, telrUrl } from 'Shared/constants/Payment'
import Loader from 'Shared/components/Loader/Loader'
import { Trans } from 'react-i18next'
import { getPriceWithCurrency } from 'Shared/helpers/payment'
import { cvSearchPricing } from 'Shared/constants/MetaTags'
import './EmployerBuyCredits.scss'

const paymentService = new PaymentService()

class CreditsCheckout extends Component {
  state = {
    ready: false,
    telrReady: false
  }

  componentDidMount () {
    this.props.fetchCurrentLocationIfNeeded()
    this.props.saveOrderRefIfNeeded()
    this.validatePayment(this.props.orderRef)
  }

  onTelrLoad = () => this.setState({telrReady: true})

  get order () {
    const { orderRef } = this.props
    const { ready, telrReady } = this.state

    return (
      <Fragment>
        { !ready && !telrReady && <Loader className='loader--component' /> }
        <iframe onLoad={this.onTelrLoad} className='credits-checkout__frame' src={telrUrl(orderRef)} />
      </Fragment>
    )
  }

  redirectToHelp = () => window.open('https://help.oliv.com/searching-the-community/cancelling-your-membership', '_blank')

  get productAmount () {
    const { t } = this.props
    return <div className='database-search-summary__wrapper'>
      <span>{t('plansPage.products.databaseSearch.title')}</span>
      <span className='database-search-summary__description'>
        <Trans i18nKey='plansPage.products.databaseSearch.summaryDescription'>
          View unlimited profiles and contact up to 50 profiles every month. Renewed every month automatically until you <a className='click-able-link' onClick={this.redirectToHelp}>cancel</a> your plan
        </Trans>
      </span>
    </div>
  }

  async validatePayment (orderRef) {
    try {
      const { data } = await paymentService.checkMembershipPlanPayment(orderRef)
      const { status } = data.order.content.order

      return this.redirectByTelrStatusCode(status.code)
    } catch (e) {
      return console.error(e)
    }
  }

  redirectByTelrStatusCode = (statusCode) => {
    const { employerId, orderRef } = this.props
    switch (statusCode) {
      case TelrOrderStatusCodes.paid:
        window.location.href = `${window.location.origin}/employer/${employerId}/payment/${PaymentStatuses.authorised}/${orderRef}`
        break
      case TelrOrderStatusCodes.cancelled:
        window.location.href = `${window.location.origin}/employer/${employerId}/payment/${PaymentStatuses.cancelled}/${orderRef}`
        break
      case TelrOrderStatusCodes.declined:
        window.location.href = `${window.location.origin}/employer/${employerId}/payment/${PaymentStatuses.declined}/${orderRef}`
        break
      default:
        return this.setState({ ready: true })
    }
  }

  render () {
    const { t, price, currency, orderRef } = this.props

    return (
      <Fragment>
        <Helmet>
          <title>{cvSearchPricing.title}</title>
          <meta name='description' content={cvSearchPricing.description} />
          <meta property='og:title' content={cvSearchPricing.title} />
          <meta property='og:type' content='website' />
          <meta property='og:image' content={cvSearchPricing.image} />
          <meta property='og:image:width' content='1200' />
          <meta property='og:image:height' content='630' />
          <meta property='og:url' content={cvSearchPricing.url} />
          <meta property='og:description' content={cvSearchPricing.description} />
        </Helmet>
        <Order
          reviewUrl={'/pricing/candidates/review'}
          checkoutUrl={`/pricing/candidates/checkout/${orderRef}`}
          order={this.order}
          productAmount={this.productAmount}
          price={getPriceWithCurrency(price, currency)}
          benefitsTitle={t('order.jobPostBenefitsTitle')}
          benefits={[
            t('plansPage.products.databaseSearch.benefits.screenedCommunity'),
            t('plansPage.products.databaseSearch.benefits.cvs'),
            t('plansPage.products.databaseSearch.benefits.filters'),
            t('plansPage.products.databaseSearch.benefits.sort')
          ]}
          showCheckoutBtn={false}
        />
      </Fragment>
    )
  }
}

export default withTranslations(CreditsCheckout)



